.home{
    display: inline-flex;
    flex-direction: row;
    justify-content: space-evenly;
    width: 100%;
    height: 100%;
}
.mainButton{
    width: 275px;
    height: 50px;
    border: 1px solid lightgray;
    line-height: 50px;
    border-radius: 5px;
    color: snow;
    background: #e83030;
    display: block;
    text-align: center;
    margin-bottom: 15px;
    font-size: larger;
    &:hover{
        color: white;
    }
}