.personInfo{
    max-width: 265px;
    width: 265px;
    background-color: #008080;
    border-radius: 7px;
    color: snow;
    padding:15px;
    height:300px;
}
.personInfo-Title{
    text-align: center;
    background-color: #006667;
    border-top-right-radius: 7px;
    border-top-left-radius: 7px;
    margin-top:-15px;
    margin-left:-15px;
    margin-right:-15px;
    padding-top:10px;
    padding-bottom:10px;
    &:hover{
        color: white;
    }
}
.personInfo-Name{
    text-align: center;
    margin-top:10px;
    margin-bottom: 20px;
}

.personInfo-Age{
    margin-left: 50px;
}

.personAverageInfo-Link{
    margin-top: 155px;
    text-decoration: underline;
    cursor: pointer;
    text-align: center;
    &:hover{
        color: white;
    }
}

.personInfo-Link{
    margin-top:90px;
    text-decoration: underline;
    cursor: pointer;
    text-align: center;
    color: white;
    
}
.personInfo-Link > a{
    color: white;
}

.lifespanLinkWrapper{
    margin-top: 155px;
    text-align: center;
}
.textLink{
    margin-top:90px;
    text-decoration: underline;
    cursor: pointer;
    text-align: center;
    color: white;
}