.bargraph {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: stretch;
  height: 320px;
  width: 90%;
  background: lightgray;
  margin-left: 5%;
  margin-top: 2%; }

.bar {
  margin: 5px;
  /*Overridden by flexbox:*/
  width: 100%;
  background: #e83030;
  position: relative;
  cursor: pointer; }
  .bar[data-title] {
    margin-bottom: 1.7rem; }
  .bar::before {
    content: attr(data-val);
    top: 0;
    color: white;
    font-weight: bold; }
  .bar::after {
    content: attr(data-title);
    color: black;
    top: 100%; }
  .bar::before, .bar::after {
    font-size: 1.2rem;
    text-align: center;
    position: absolute;
    left: 0;
    right: 0; }
  .bar.flip-percentage::before {
    top: unset;
    bottom: 100%;
    color: black; }

